export type IdentityJourneyConfiguration = Record<string, JourneyConfigKeyValuePair>;

export type JourneyConfigValueType =
  | string
  | number
  | boolean
  | Array<string | Record<string, any> | undefined>
  | undefined;

export type JourneyConfigKeyValuePair = {
  [prop: string]: JourneyConfigValueType;
};

export type JourneyCommonPropertyTypes = {
  notificationDismissTime: number;
  emailAddressTypes: Array<string>;
  postalAddressTypes: Array<string>;
  phoneNumberTypes: Array<string>;
  maxEmailAddresses: number;
  maxPhoneNumbers: number;
  maxPostalAddresses: number;
  showWidgetHeader: boolean;
  phoneNumberCountryCode: CountryCode;
  phoneNumberDebounceTime: number;
  enablePhoneNumberFormatter: boolean;
  showEditableLanguage: boolean;
  itemsPerPage: number;
  maxNavPages: number;
  legalEntityDebounceTime: number;
  legalEntityResponseSize: number;
  usernameDebounceTime: number;
  displaySetEntitlements: boolean;
  displayViewUserDetails: boolean;
  lockUnlockUserAccountCommentRequired: boolean;
  routeParamNameUserId: string;
};
